.connect_with_us_2 {
  background: #fff;
  .container-fluid {
    width: 100%;
    padding: 0;
    margin: 0;
  }
  .container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 130px 60px 70px 80px;
    @media screen and (max-width: 1100px) {
      max-width: 1100px;
      padding: 32px 16px;
    }
    @media screen and (max-width: 768px) {
      max-width: 720px;
      padding: 32px 16px;
    }
    @media screen and (max-width: 480px) {
      max-width: 375px;
      padding: 32px 16px;
    }
  }
  .three_boxes {
    display: flex;
    @media screen and (max-width:480px) {
      flex-flow: wrap;
    }
    .left-image-box {
        display: grid;
        align-content: end;
        @media screen and (max-width:480px) {
          width: 50%;
          order: 1;
        }
    }
    .right-image-box {
      display: block;
      margin-top: -5%;
      @media screen and (max-width:480px) {
        width: 50%;
        order: 2;
      }
    }
    .intro {
      display: grid;
      gap: 12px;
      margin-bottom: 40px;
      text-align: center;
      max-width: 716px;
      width: 100%;
      @media screen and (max-width:480px) {
        padding: 24px 0;
        width: 100%;
        order: 3;
        margin-bottom: 10px;
      }

      .title {
        display: grid;
        gap: 24px;
        padding: 0;

        h2 {
          margin: 0;
          font-weight: 500;
          color: #0a2244;
        }

        p {
          color: #616d82;
        }
      }

      .subtitle {
        font-family: "General Sans Variable";
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 20px;
        text-transform: uppercase;
        color: #828fa5;
        justify-content: center;
        justify-self: center;
        text-align: center;
      }

      .btn {
        position: relative;
        text-align: center;
        border-radius: 25px;
        background: #f76c34;
        color: #fff;
        width: 158px;
        height: fit-content;
        transition: 0.4s;
        padding: 13px 10px;
        margin: 20px auto 0;
        &:hover {
          background: #fff;
          border: 1px solid #f76c34!important;
          border: 0;
          color: #f76c34;
        }

        span {
          font-family: "General Sans Variable";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 22px;
          margin: 0;
          padding: 0;
        }

        a {
          position: absolute;
          height: 100%;
          width: 100%;
          left: 0;
          top: 0;
          z-index: 1;
        }
      }
    }
  }
}
